.carousel {
  width: 100%;
  height: 100%;
}
.carousel video {
  display: block;
  width: 100%;
  height: 100%;
}
.carousel-cell {
  width: 100vw;
  height: 100vh;
}
.carousel.is-fullscreen .carousel-cell {
  height: 100%;
}
.carousel .flickity-viewport {
  pointer-events: none;
}
.carousel .flickity-button {
  background-color: transparent;
  padding: 0px;
  color: white;
}
.carousel .flickity-button:focus {
  box-shadow: none;
}
.carousel .carousel-cell .z-index {
  bottom: 0;
  position: absolute;
  height: auto;
  width: 100%;
  left: 0;
  top: 65vh;
}
@media (min-width: 768px) {
  .carousel .carousel-cell .z-index {
    top: 70vh;
  }
}
.carousel .carousel-cell .z-index .slider-header {
  opacity: 0;
  transition: all 2s ease-in-out;
  padding-bottom: 2rem;
  display: block;
  pointer-events: visible;
}
.carousel .carousel-cell .z-index .slider-header h1, .carousel .carousel-cell .z-index .slider-header h2 {
  color: white;
  text-shadow: 2px 2px black;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4em;
}
@media (min-width: 992px) {
  .carousel .carousel-cell .z-index .slider-header h1, .carousel .carousel-cell .z-index .slider-header h2 {
    font-size: 2em;
  }
}
.carousel .carousel-cell .z-index .slider-header i {
  font-size: 32px;
  margin-left: 16px;
  padding-top: 10px;
  transition: all 0.4s ease-in-out;
}
.carousel .carousel-cell .z-index .slider-header:hover i {
  margin-left: 32px;
}
.carousel .carousel-cell.is-selected .slider-header {
  opacity: 1;
}
.carousel .carousel-cell .video-credit {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  text-align: right;
  color: white;
  z-index: 2;
  font-size: 16px;
}
@media (min-width: 768px) {
  .carousel .carousel-cell .video-credit {
    right: 167px;
  }
}
.carousel .flickity-page-dots {
  display: none;
}
.carousel .arrows {
  width: 60px;
  height: 72px;
  position: absolute;
  left: 50%;
  margin-left: -30px;
  bottom: 20px;
}
.carousel .arrows path {
  stroke: #fff;
  fill: transparent;
  stroke-width: 2px;
  animation: arrow 2s infinite;
  -webkit-animation: arrow 2s infinite;
}
@keyframes arrow {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes arrow /*Safari and Chrome*/ {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.carousel .arrows path.a1 {
  animation-delay: -1s;
  -webkit-animation-delay: -1s;
  /* Safari  Chrome */
}
.carousel .arrows path.a2 {
  animation-delay: -0.5s;
  -webkit-animation-delay: -0.5s;
  /* Safari  Chrome */
}
.carousel .arrows path.a3 {
  animation-delay: 0s;
  -webkit-animation-delay: 0s;
  /* Safari  Chrome */
}

.video-template {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
