    margin: 0em;
    bottom: 25px;
  }
  .video-template .video-container .h1-container .inner-container {
    justify-content: space-between;
    padding: 0em;
  }
  .video-template .video-container .h1-container .inner-container h4 {
    padding: 1em;
    margin: 0;
  }
  .video-template .video-container .h1-container .inner-container .left, .video-template .video-container .h1-container .inner-container .right {
    min-height: 100px;
    padding: 1em;
  }
  .video-template .video-container .h1-container .inner-container .left {
