.carousel{
  width:100%;
  height:100%;
      video{
      display: block;
      width: 100%;
      height: 100%;
    }
      &-cell {
      width: 100vw;
      height: 100vh;
    }
      &.is-fullscreen .carousel-cell {
      height: 100%;
    }
  // enable if you want buttons to show/work
  //   button{
  //       z-index:2;
  //   }
//   .flickity-page-dots{
//     display:none;
// }
// button{
//     display:none;
// }
.flickity-viewport{
  pointer-events: none;
}
.flickity-button{
  background-color:transparent;
  padding:0px;
  color:white;
  &:focus{
    box-shadow:none;
  }
}
.carousel-cell{

  .z-index{
    bottom:0;
    position: absolute;
    height:auto; 
    width: 100%;
    left:0;
    top:65vh;
    @include md-screen{

      top:70vh;
    }
    .slider-header{
      opacity:0;
      transition:all 2s ease-in-out;
      padding-bottom:2rem;
      // top:220px;
      display: block;
      // height:50vh;


      pointer-events:visible;

      h1,h2{
        color:white;
        text-shadow: 2px 2px black;
        display:flex;
        align-items: center;
        justify-content: center;
        font-size:1.4em;
        @include lg-screen{
          font-size:2em;
        }
      }

      i{
        font-size:32px;
        margin-left:16px;
        padding-top:10px;
        transition:$menu_transition;
      }
      &:hover{
        i{
          margin-left:32px;
        }

      }
      }
    }
    &.is-selected{
      .slider-header{
        opacity:1;
      }
  }
  .video-credit{
    position: absolute;
    bottom:0;
    right:0;
    width:100%;
    text-align:right;
    color:white;
    z-index: 2;
    font-size:16px;
    @include md-screen{
      right:167px;
    }
  }
 
}
.flickity-page-dots{display:none;}
.arrows {
	width: 60px;
	height: 72px;
	position: absolute;
	left: 50%;
	margin-left: -30px;
	bottom: 20px;
}

.arrows path {
	stroke: #fff;
	fill: transparent;
	stroke-width:2px;	
	animation: arrow 2s infinite;
	-webkit-animation: arrow 2s infinite; 
}

@keyframes arrow
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

@-webkit-keyframes arrow /*Safari and Chrome*/
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

.arrows path.a1 {
	animation-delay:-1s;
	-webkit-animation-delay:-1s; /* Safari  Chrome */
}

.arrows path.a2 {
	animation-delay:-0.5s;
	-webkit-animation-delay:-0.5s; /* Safari  Chrome */
}

.arrows path.a3 {	
	animation-delay:0s;
	-webkit-animation-delay:0s; /* Safari  Chrome */
}
}
